export default {
	data(){
		return{
			scrollwidth: 0
		}
	},
	props:['tabIndex','tarBars'],
	methods:{
		tabtap(index){
			this.$emit('tabtap',index)
		}
	},
	watch:{
		tabIndex(){
			if(this.tabIndex>=3){
				this.scrollwidth = 80
			}
			if(this.tabIndex<3){
				this.scrollwidth = 0
			}
		}
	}
}
